import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddUsersComponent } from './add-users/add-users.component';
import { ListUsersComponent } from './list-users/list-users.component';
import { ListOnlineUsersComponent } from './list-online-users/list-online-users.component';
import { EditUsersComponent } from './edit-users/edit-users.component';
import { SendNotifcationsComponent } from './send-notifcations/send-notifcations.component';
import { VideoLogComponent } from './video-log/video-log.component';
import { AlertVideoUserComponent } from './alert-video-user/alert-video-user.component';

const routes: Routes = [
  {
    path:'add-user',
    component:AddUsersComponent
  },
  {
    path:'list-online-user',
    component:ListOnlineUsersComponent
  },
  {
    path:'list-user',
    component:ListUsersComponent
  },
  {
    path:'edit-user/:id',
    component:EditUsersComponent
  },
  {
    path:'video-log/:id',
    component:VideoLogComponent
  },
  {
    path:'send-notification',
    component:SendNotifcationsComponent
  },
  {
    path:'alert-user',
    component:AlertVideoUserComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManageUsersRoutingModule { }
