import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdminService } from '../../../admin.service';
import { NotificationService } from '../../../../shared/service/notifiation.service';
import * as Ladda from 'ladda';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-send-notifcations',
  templateUrl: './send-notifcations.component.html',
  styleUrls: ['./send-notifcations.component.css']
})
export class SendNotifcationsComponent implements OnInit {
  sendNotificationForm: FormGroup;
  isLoading = false ;
  constructor(private adminService: AdminService,
    private notification: NotificationService,
    private spinner: NgxSpinnerService) { }
  ngOnInit() {
    this.sendNotificationForm = new FormGroup({
      message: new FormControl('', [
        Validators.required,
      ]),
      title: new FormControl('', [
        Validators.required,
      ]),
      gender: new FormControl('', [
        Validators.required,
      ])

    });
  }

  onNotifications() {
    if (this.sendNotificationForm.valid) {
      this.spinner.show();
      console.log(this.sendNotificationForm.value, 'this.sendNotificationForm.value');
      this.adminService.sendNotifcations(this.sendNotificationForm.value).subscribe(res => {
        console.log('response', res);
        // var l = Ladda.create(document.querySelector('#ladda-button'));
        // l.start()
        if (res.code === 200) {
          // l.stop()
          this.isLoading = !this.isLoading;
          this.spinner.hide();

          this.notification.notificationSent(res.message);
        } else {
          // l.stop()
          this.isLoading = !this.isLoading;
          this.spinner.hide();

          this.notification.notificationSent(res.message);

        }
      });
    } else {

    }
  }

}
