import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../admin.service';

@Component({
  selector: 'app-alert-video-user',
  templateUrl: './alert-video-user.component.html',
  styleUrls: ['./alert-video-user.component.css']
})
export class AlertVideoUserComponent implements OnInit {

  constructor(private admin: AdminService) { }
  userInfos: any
  ngOnInit() {
    this.userInfoAbusiveAlert()
  }


  userInfoAbusiveAlert() {
    this.admin.userInfoAbusiveAlert().subscribe(res => {
      console.log('info',res.data)
      // console.log('77777777777777777777777',res.data[0].)
      this.userInfos=res.data
    })
  }
}
