	K:any;C:any;S:any;T:any;
  ngOnInit() {
    // this.exportDataToCSV();
    this.checkBoxForm = this.fb.group({
      userid: this.fb.array([])
    });
    // this.spinner.show()
    // setTimeout(() => {
    //   this.spinner.hide()
    // }, 1000)
    this.editAgeForm = new FormGroup({
      id: new FormControl(''),
      DOB: new FormControl('', [
        Validators.required,
      ])

    });
    this.searchForm = new FormGroup({
      searchText: new FormControl('', [Validators.required, Validators.maxLength(20)]),
    });
    this.sendNotificationForm = new FormGroup({
      id: new FormControl(''),
      title: new FormControl('', [
        Validators.required,
      ]),
      message: new FormControl('', [
        Validators.required,
      ])

    });
    this.editGenderForm = new FormGroup({
      id: new FormControl(''),
      gender: new FormControl('', [
        Validators.required,
      ])

    });
    this.listUsers(this.page);
    this.ageList();
	this.genderSelected = '';
  }


  enableDisableGenderBasedAlarm(flag) {
    // console.log('genderalarm', this.isDisabledFemale, flag);
    this.notifcation.confirm('You want to enable alarm').then(data => {
      // console.log('dataaaaa', data);
      if (data.value) {
        const gendervalue = {
          // tslint:disable-next-line:radix
          gender: parseInt(this.isDisabledFemale[0]),
          enableOrDisableAlarm: !flag
        };
        this.adminService.enableOrDisableGenderAlarm(gendervalue).subscribe(res => {
          if (res.code === 200) {
            this.isDisabledFemale = '';
            this.listUsers(0);
          } else {
            this.isDisabledFemale = '';
            this.listUsers(0);
          }
        });
      } else {
        this.isDisabledFemale = '';
      }
    });
  }

  ageList() {
    this.adminService.ageList().subscribe(res => {
      // console.log(res.data, '************');
      if (res.code === 200) {
        this.ageLists = res.data;

      } else {

      }
    });
  }


