  constructor(private admin: AdminService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private elRef: ElementRef,
	 private toaster: ToastrService, private toastr: ToastrService,
    private fb: FormBuilder) {
    this.rForm = fb.group({
      add: this.fb.array([]),
    });
    // this.addData()
    this.addNotes = new FormGroup({
		notes:new FormControl('', [
			Validators.required,
		]),
	});	
	this.editReviewForm = new FormGroup({
      rId: new FormControl(''),
      isInterested: new FormControl('', [
        Validators.required,
      ]),
      pleasant: new FormControl('', [
        Validators.required,
      ]),
      attractive: new FormControl('', [
        Validators.required,
      ]),
      marriageFocused: new FormControl('', [
        Validators.required,
      ]),
      notes: new FormControl('', [
        Validators.required,
      ])
    });
  }
