  changeValue(review_id,changed_value){
		const obj = {
			review_id: review_id,
			value : changed_value
		}
		  this.admin.changeReviewInterest(obj).subscribe(res => {
			console.log('changeReviewInterest', res);
			if (res.code == 200) {
				this.displayReviews = false;
				this.toastr.success('Interest has been updated successfully.');
				this.videoCallDone();
			} else if (res.code == 404) {
				this.displayReviews = false;
				this.toastr.success('Some error occurred in updating Interest. Please try again.');
				this.videoCallDone();
				
			}

		  });
	}
	
  
